    const carousel = document.querySelector('#carousel');
    const width = carousel.offsetWidth;
    const height = carousel.offsetHeight;

    const stage = new Konva.Stage({
        container: 'carousel',
        width: width,
        height: height
    });

    const layer = new Konva.Layer();

    const circle = new Konva.Circle({
        x: stage.width() / 2,
        y: stage.height() / 2,
        radius: 95,
        // fill: 'red',
        stroke: '#7c7c7c',
        strokeWidth: 1
    });

for (let i = 1; i <= 5; i++) {
    const angle = (i * 72 - 36) * Math.PI / 180;
    const circle_x = circle.radius() * Math.cos(angle);
    const circle_y = circle.radius() * Math.sin(angle);
    var line = new Konva.Line({
        x: circle.x(),
        y: circle.y(),
        points: [circle_x, circle_y, 110 * Math.cos(angle), 110 * Math.sin(angle)],
        stroke: circle.stroke(),
        strokeWidth: 1,
        lineCap: 'round',
        lineJoin: 'round'
    });
    layer.add(line);
}



    // add the shape to the layer
    layer.add(circle);
    

    // add the layer to the stage
    stage.add(layer);

    var anim = new Konva.Animation(function(frame) {
        var dist = 50 * (frame.timeDiff / 1000);
        line.move(dist, 0);
    }, layer);
      
      anim.start();


    function fitStageIntoParentContainer() {
        var container = document.querySelector('#carousel');

        // now we need to fit stage into parent
        var containerWidth = container.offsetWidth;
        // to do this we need to scale the stage
        var scale = containerWidth / width;


        stage.width(width * scale);
        stage.height(height * scale);
        stage.scale({ x: scale, y: scale });
        stage.draw();
    }

    fitStageIntoParentContainer();
    // adapt the stage on any window resize
    window.addEventListener('resize', fitStageIntoParentContainer);

